@use './../base/mixin' as *;
@use './../base/vars' as *;

.portfolio {
    padding: 50px 0 ;

    @include mobileX {
        padding: 15px 0 70px;
    }
}

.porfolio__title {
    font-size: 22px;
    margin-bottom: 60px;
    font-weight: 600;
    
    em{
        color: #5454D4;
    }
    @include mobileX {
        margin-bottom: 20px;
        font-size: 18px;
    }
    
}

.portfolio__wrapper {
    display: flex;
    flex-direction: column;
    gap: 100px;
    align-items: center;

    @include mobileX {
        gap: 50px ;
    }
}

.portfolio__content {
    display: flex;
    gap: 70px;
    text-align: center;
    height: 17rem;
    

    &:nth-child(2n){
        flex-direction: row-reverse;
        @include tablet {
            flex-direction: column;
        }
    }

    @include tablet {
        flex-direction: column;
        height: unset;
        gap: 20px;
        padding: 35px 0 20px;
        border-top: 1px solid #5454D4;
        border-bottom:1px solid #5454D4;
        border-radius: 17px;
    }
    @include mobileX {
        gap: 10px;
    }
}

.portfolio__content-preview {
    max-width: 400px;
    border-radius: 5px;

    @include mobileX {
        max-width: 100%;
    }
}
.portfolio__content-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.portfolio__content-text__title {
    font-size: 18px;
    margin-bottom: 20px;
    opacity: 0.8;

    @include mobileX {
        margin-bottom: 10px;
    }
}
.portfolio__content-text-desc {
    max-width: 400px;
    margin-bottom: 30px;
    line-height: 1.5;

    @include mobileX {
        font-size: 14px;
        margin-bottom: 10px;
    }
}

.portfolio__content-text__tech {
    display: flex;
    gap: 30px;
    font-size: 18px;
    margin-bottom: 40px;
    pointer-events: none;
    span{
        color: #76a0c5;
        text-transform: uppercase;
    }

    @include mobileX {
        font-size: 16px;
        gap: 15px;
        margin-bottom: 15px;
    }
}

.portfolio__content-text__demo {
    display: flex;
    gap: 50px;
}
.portfolio__content-text__link {
    color: #363535;
    position: relative;
    font-size: 18px;
    transition: .2s ease-in;
    border: 1px solid rgba(160, 153, 153, 0.7);
    border-radius: 30px;
    padding: 4px 25px;

    @include mobileX {
        font-size: 16px;
    }

    // &:hover{
    //     background-color: rgba(255, 255, 255, 0.253);
    // }

    &:hover{
        &::before{
            content: '';
            position: absolute;
            height: 28px;
            background-color: rgba(114, 110, 110, 0.4);
            border-radius: 30px;
            left: 0;
            bottom: 0;
            animation: solid 0.45s forwards ;
        }
    }

    &:focus,
    &:focus-visible{
        &::before{
            content: '';
            position: absolute;
            height: 28px;
            background-color: rgba(114, 110, 110, 0.4);
            border-radius: 30px;
            left: 0;
            bottom: 0;
            animation: solid 0.45s forwards ;
            outline: 2px solid rgba(160, 153, 153, 0.7);
            border:unset;
        }
    }
}