@use '../../scss/base/mixin' as*;

$lineWidth: 30px;
$lineHeight: 4px;
$color:#fff;

$width: $lineWidth;
$height: $lineHeight * 5;

.nav-icon {
    width: $width;
    height: $height;
    display: none;
    flex-grow: 1;
    justify-content: flex-end;
	align-items: center;
    cursor: pointer;
    z-index: 999;

    @include tablet {
        display: flex;
    }
   
}

.nav-icon__middle {
    position: relative;
    width: $lineWidth;
    height: $lineHeight;
    background-color: $color;
    border-radius: 2px;

    &::before, &::after {
        content: "";
        position: absolute;
        left: 0;
        width: $lineWidth;
        height: $lineHeight;
        border-radius: 2px;
        transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
        background-color: $color;
    }

    &::before {
        top: -($lineHeight*2);
    }

    &::after {
        top: $lineHeight*2;
    }
}

.nav-icon.nav-icon--active {
    position: fixed;
    top: 40px;
    right: 15px;
    @include mobileX {
        top: 20px;
    }
}

.nav-icon.nav-icon--active .nav-icon__middle {
    background-color: transparent;
    $color: #fff;

    &::before, &::after {
        top: 0;
        transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
        background-color: $color;
    }

    &::before {
        transform: rotate(45deg);
    }

    &::after {
        transform: rotate(-45deg);
    }
}



