@use './../base/vars' as*;
@use './../base/mixin' as*;

.nav {
    padding: 28px 0 20px;
    background-color: #1a1a19;
    border-bottom: 1px solid #26292d;
    
    @include mobileX {
        padding: 15px 0;
    }
}

.nav__row{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;

    @include tablet {
        gap:20px;
    }
}

.nav__logo {
    font-size: clamp(1rem, 0.679rem + 1.43vw, 1.75rem);
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: 700;
    letter-spacing: 2px;
    color: $dark-color-text !important;
    
    @include tablet {
        img{
            width: 30px;
        }
    }
}

.nav__list {
    display: flex;
    flex-grow: 1;
    justify-content: end;
    align-items: center;
    gap: 30px;
    z-index: 999;

    @include tablet {
                position: fixed;
                top: -100%;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: #2D2D2D;
                opacity: 0;
        
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                transition: opacity .2s ease-in, top 0s linear 0.2s,;
    }

    &--active{
        top: 0;
        opacity: 1;
        transition: top 0s linear, opacity .2s ease-in;
    }
}


.nav__list-item {
    font-weight: 600;
    font-size: 16px;
}

.nav__list-link {
    color: $dark-color-text;
    text-transform: uppercase;
    position: relative;
    padding-bottom: 5px;

    &:hover{
        &::before{
            content: '';
            position: absolute;
            height: 3px;
            background-color: #5454D4;
            border-radius: 3px;
            left: 0;
            bottom: 0;
            animation: solid 0.25s linear forwards ;
        }
    }
}

.nav__list-link--active{
    color: $dark-color-link;
    &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: #5454D4;
        border-radius: 3px;
        left: 0;
        bottom: 0;
    }

    &:hover{
        &::before{
            animation: none;
        }
    }
}

@keyframes solid {
    from{
        width: 0;
    }
    to{
        width: 100%;
    }
}

