@use '../../scss/base/vars' as *;

.dark-mode{
    position: relative;
    display: flex;
    gap: 6px;
    background-color: #272727;
    padding: 5px 5px;
    border-radius: 50px;
    img{
        z-index: 99;
    }
    
    &::before{
        content: '';
        position: absolute;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        top: 2px;
        left: 2px;
        background-color: #fff;
        transition: 0.2s ease-in;
    }
}

.dark-mode.dark-mode--switch{
    &::before{
        left: 23px;
        transition: 0.2s ease-in;
        background-color: $yellow;
    }
}