@use './../base/vars' as*;
@use './../base/mixin' as *;

.header{
    padding: 40px 0;
    overflow: hidden;

    @include tablet {
        padding: 20px 0;
    }
}

.header__content{
    min-height: 628px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: relative;

    @include tablet {
        min-height: 500px;
    }
    @include mobileX {
        min-height: 390px;
    }
    @include mobile {
        min-height: 345px;
    }
}

.header__content-title {
    font-size: clamp(1.125rem, 0.625rem + 2.5vw, 2.5rem);
    margin-bottom: 30px;

    display: flex;
    flex-direction: column;
    align-items: center;
    line-height: 1.7;

    strong{
        font-size: clamp(1.625rem, 0.852rem + 3.86vw, 3.75rem);
        display: flex;
    }
    em{
        font-style: normal;
        color: $accent-color;
        position: relative;
        

        &::before{
            content: '';
            position: absolute;
            width: 4px;
            height: 1em;
            top: clamp(0.5rem, 0.227rem + 1.36vw, 1.25rem);
            right: -10px;
            background-color: $accent-color;
            border-radius: 1px;
            animation: flashing 1.3s ease-in-out infinite;
        }
    }
}

@keyframes flashing {
    0%{
        background-color: $accent-color;
    }50%{
        background-color: transparent;
    }100%{
        background-color: $accent-color;
    }
}

.header__content-subtitle {
    font-size: clamp(0.875rem, 0.784rem + 0.45vw, 1.125rem);
    opacity: 0.7;
}

.header__square{
    position: absolute;
    top: 10vh;
    left: 30%;

    @include tablet {
        left: 25%;
    }
    @include mobileX {
        left: 20%;
    }
    @include mobile {
        left: 12%;
    }
}



