.square{
    max-width: 444px;
    max-height: 444px;
    z-index: -11;
}

.square-ani{
    width: clamp(13.875rem, 8.83rem + 25.23vw, 27.75rem);
    height: clamp(13.875rem, 8.83rem + 25.23vw, 27.75rem);
    position: absolute;
    color: aqua;
    // border: 2px solid rgba(0, 0, 0, 0.1);
    border: 2px solid rgba(84, 84, 212, 0.2);
    
    top: 0; 
    left:0; 
    animation: rotateForward 15s linear infinite ;

    // &--dark-mode{
    //     border: 2px solid rgba(255, 255, 255, 0.1);
    // }
}

#square1{animation: rotateForward 40s linear infinite ;}
#square2{animation: rotateBackward 28s linear infinite ;}
#square3{transform: rotate(60deg); animation: rotateForwardTo 30s linear infinite ;}
#square4{transform: rotate(-60deg); animation: rotateBackwardTo 37s linear infinite ;}
#square5{transform: rotate(180deg);animation: rotateForwardTo 36s linear infinite ;}

@keyframes rotateForward {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(180deg); }
    100% { transform: rotate(360deg); }
}

@keyframes rotateForwardTo {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(180deg); }
    100% { transform: rotate(360deg); }
}

@keyframes rotateBackward {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(-180deg); }
    100% { transform: rotate(-360deg); }
}
@keyframes rotateBackwardTo {
    0% { transform: rotate(0deg); }
    50% { transform: rotate(-180deg); }
    100% { transform: rotate(-360deg); }
}


