@use './../base/vars' as*;
@use './../base/mixin' as*;

.about{
    padding: 50px 0;

    @include mobileX {
        padding: 15px 0 30px;
    }
}

.about__title{
    font-size: 22px;
    margin-bottom: 60px;
    font-weight: 600;
    line-height: 1.6;

    em{
        color: $accent-color;
    }

    @include mobileX {
        font-size: 18px;
        margin-bottom: 20px;
    }
}

.about__stack{
    font-size: 22px;
    display: flex;
    justify-content: end;
    align-items: center;
    gap: 50px;

    text-transform: uppercase;
    margin-bottom: 30px;
    
    @include tablet {
        justify-content: center;
        font-size: 18px;
    }
    @include mobileX {
        margin-bottom: 0;
    }
}


.about__stack-tech{
    display: flex;
    align-items: center;
    gap: 15px;

    position: relative;

    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: -20px;
        width: 3px;
        height: 100%;
        background-color: $accent-color;
    }
    @include tablet {
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
}

.about__stack-tech__react{
    animation: rotation 9s linear infinite;
}

.about__stack-tech__sass{
    animation: updown 2s ease-in-out infinite;
}

@keyframes rotation {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

@keyframes updown {
    0%{
        transform: translateY(-3px);
    }
    50%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(-3px);
    }
}

.about__wrapp {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 80px;
    width: 100%;

    @include tablet {
        flex-direction: column;
        gap: 40px;
    }

    @include mobileX {
        gap: 10px; 
    }
}

.about__img{
    max-width:350px;
    text-align: center;
}

.about__me {
    max-width: 50%;

    @include tablet {
        max-width: 80%;
    }
}

.about__me-title{
    display: block;
    font-size: 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
    opacity: 0.7;
}

.about__me-subtitle {
    font-size: clamp(1.125rem, 0.852rem + 1.36vw, 1.875rem);
    margin-bottom: 10px;
}

.about__me-desc {
    line-height: 1.3;
    opacity: 0.8;

    @include tablet {
       font-size: 14px;
    }
}