@use './../base/mixin' as *;

.footer {
    margin-top: auto;
    padding: 20px 0;
    background-color: #1a1a19;
    border-top: 1px solid #26292d;

    @include mobileX {
        padding: 10px 0;
    }
}

.footer__wraper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;

    p{
        font-size: 14px;
        color: rgba(255, 255, 255, 0.7);
    }
}

.footer__social{
    display: flex;
    align-items: center;
    gap: 15px;

    
    svg{
        fill: #fff;
        stroke: #fff;
        width: 30px;
        height: 30px;
        transition: 0.2s ease-in;

        &:hover{
            fill: #f2ff47;
            stroke: #f2ff47;
            scale: 1.2;
        }
    }
}

