@use './../base/vars' as *;
@use './../base/mixin' as *;

.contact {
    padding: 50px 0;

    @include mobileX {
        padding: 15px 0 30px;
    }
}

.contact__title {
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 70px;

    em{
        color: $accent-color;
    }

    @include mobileX {
        font-size: 18px;
        margin-bottom: 20px;
    }
}

.contact__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 60px;
    
    a{
        color: $light-color-link;
    }

    &--dark {
        a{
            color: $dark-color-link;
        }
    }

    @include mobileX {
        gap: 40px;
        height: 60vh;
        
    }
}

.contact__location,
.contact__number,
.contact__mail {
    font-size: 30px;
    text-align: center;
    h2{
        margin-bottom: 10px;
        font-weight: 700;
    }
    a{
        font-size: 18px;
        transition: 0.2s ease-in;
        
        @include mobileX {
            font-size: 14px;
        }

        &:hover{
            color: $accent-color;
        }
    }

    @include mobileX {
        font-size: 24px;
    }
}

.contact__arrow{
    text-transform: uppercase;
    text-align: center;
    position: relative;
    color: $accent-color;
    font-size: 18px;
    font-weight: 500;

    &::before{
        content: '';
        position: absolute;
        bottom: 0;
        background-image: url(../../img/arrows/arrow-down-uni.svg);
        background-repeat: no-repeat;
        background-size: contain;
        width: 27px;
        height: 40px;
        transform: translate(-50%,calc(100% * 2));
        animation: down 2s ease-in-out infinite;
    }
}

@keyframes down {
    0%{
        transform: translate(-50%,calc(100% * 2));;
    }
    50%{
        transform: translate(-50%,calc(100% * 2.5));;
    }
    100%{
        transform: translate(-50%,calc(100% * 2));;
    }
}
